import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import yonghuList from '@/views/pages/yonghu/list'
import yonghuDetail from '@/views/pages/yonghu/formModel'
import yonghuAdd from '@/views/pages/yonghu/formAdd'
import yonghuRegister from '@/views/pages/yonghu/register'
import yonghuCenter from '@/views/pages/yonghu/center'
import renwufenleiList from '@/views/pages/renwufenlei/list'
import renwufenleiDetail from '@/views/pages/renwufenlei/formModel'
import renwufenleiAdd from '@/views/pages/renwufenlei/formAdd'
import shixunrenwuList from '@/views/pages/shixunrenwu/list'
import shixunrenwuDetail from '@/views/pages/shixunrenwu/formModel'
import shixunrenwuAdd from '@/views/pages/shixunrenwu/formAdd'
import renwuxuanzeList from '@/views/pages/renwuxuanze/list'
import renwuxuanzeDetail from '@/views/pages/renwuxuanze/formModel'
import renwuxuanzeAdd from '@/views/pages/renwuxuanze/formAdd'
import chengjifankuiList from '@/views/pages/chengjifankui/list'
import chengjifankuiDetail from '@/views/pages/chengjifankui/formModel'
import chengjifankuiAdd from '@/views/pages/chengjifankui/formAdd'
import ziyuangongxiangList from '@/views/pages/ziyuangongxiang/list'
import ziyuangongxiangDetail from '@/views/pages/ziyuangongxiang/formModel'
import ziyuangongxiangAdd from '@/views/pages/ziyuangongxiang/formAdd'
import storeupList from '@/views/pages/storeup/list'
import newsList from '@/views/pages/news/list'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'renwufenleiList',
			component: renwufenleiList
		}, {
			path: 'renwufenleiDetail',
			component: renwufenleiDetail
		}, {
			path: 'renwufenleiAdd',
			component: renwufenleiAdd
		}
		, {
			path: 'shixunrenwuList',
			component: shixunrenwuList
		}, {
			path: 'shixunrenwuDetail',
			component: shixunrenwuDetail
		}, {
			path: 'shixunrenwuAdd',
			component: shixunrenwuAdd
		}
		, {
			path: 'renwuxuanzeList',
			component: renwuxuanzeList
		}, {
			path: 'renwuxuanzeDetail',
			component: renwuxuanzeDetail
		}, {
			path: 'renwuxuanzeAdd',
			component: renwuxuanzeAdd
		}
		, {
			path: 'chengjifankuiList',
			component: chengjifankuiList
		}, {
			path: 'chengjifankuiDetail',
			component: chengjifankuiDetail
		}, {
			path: 'chengjifankuiAdd',
			component: chengjifankuiAdd
		}
		, {
			path: 'ziyuangongxiangList',
			component: ziyuangongxiangList
		}, {
			path: 'ziyuangongxiangDetail',
			component: ziyuangongxiangDetail
		}, {
			path: 'ziyuangongxiangAdd',
			component: ziyuangongxiangAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'newsList',
			component: newsList
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
