<template>
	<div ref="dragDiv" class="drag" :style="{'background-color':color}">
		<div ref="drag_bg" class="drag_bg" :style="{'background-color':activeColor}" />
		<div ref="drag_text" class="drag_text">{{ confirmWords }}</div>
		<div class="slide-check-tip" v-if="showType">请滑动验证</div>
		<div ref="moveDiv" :class="{'handler_ok_bg':confirmSuccess}" class="handler handler_bg"
			style="position: absolute;top: 0px;left: 0px;" @mousedown="mousedownFn($event)" />
	</div>
</template>

<script>
	export default {
		props: {
			color: {
				type: String,
				default: '#e8e8e8'
			},
			activeColor: {
				type: String,
				default: '#7ac23c'
			},
		},
		setup(props) {
			return {
				props
			};
		},
		data() {
			return {
				beginClientX: 0,
				/* 距离屏幕左端距离 */
				mousePressing: false,
				/* 是否正则按压滑动条 */
				maxwidth: '',
				/* 拖动最大宽度，依据滑块宽度算出来的 */
				confirmWords: '按住滑块，拖动到最右边',
				/* 滑块文字 */
				confirmSuccess: false,
				/* 验证成功判断 */
				showType: true
			};
		},
		mounted() {
			document.getElementsByTagName('html')[0].addEventListener('mousemove', this.mouseMoveFn);
			document.getElementsByTagName('html')[0].addEventListener('mouseup', this.moseUpFn);
		},
		methods: {
			mousedownFn: function(e) {
				console.log(' mousedownFn ');
				this.initMaxWidth();
				if (!this.confirmSuccess) {
					e.preventDefault && e.preventDefault(); // 阻止文字选中等 浏览器默认事件
					this.mousePressing = true;
					this.beginClientX = e.clientX;
				}
			}, // mousedoen 事件
			successFunction() {
				this.confirmSuccess = true;
				this.confirmWords = '验证通过';
				if (window.addEventListener) {
					document.getElementsByTagName('html')[0].removeEventListener('mousemove', this.mouseMoveFn);
					document.getElementsByTagName('html')[0].removeEventListener('mouseup', this.moseUpFn);
				} else {
					document.getElementsByTagName('html')[0].removeEventListener('mouseup', () => {});
				}
				this.$refs.drag_text.style.color = '#fff';
				this.$refs.moveDiv.style.left = this.maxwidth + 'px';
				this.$refs.drag_bg.style.width = this.maxwidth + 'px';
				this.$emit('success')
				this.showType = false
			}, // 验证成功函数
			mouseMoveFn(e) {
				if (this.mousePressing) {
					console.log('mouseMoveFn');
					const width = e.clientX - this.beginClientX;
					if (width > 0 && width <= this.maxwidth) {
						this.$refs.moveDiv.style.left = width + 'px';
						this.$refs.drag_bg.style.width = width + 'px';
					} else if (width > this.maxwidth) {
						this.successFunction();
					}
				}
			}, // mousemove事件
			moseUpFn(e) {
				console.log('moseUpFn');
				this.mousePressing = false;
				var width = e.clientX - this.beginClientX;
				if (width < this.maxwidth) {
					// document.getElementsByClassName('handler')[0].style.left = 0 + 'px'
					// document.getElementsByClassName('drag_bg')[0].style.width = 0 + 'px'
					this.$refs.moveDiv.style.left = 0 + 'px';
					this.$refs.drag_bg.style.width = 0 + 'px';
				}
			}, // mouseup事件,
			initMaxWidth() {
				this.maxwidth = this.$refs.dragDiv.clientWidth - this.$refs.moveDiv.clientWidth;
				// this.maxwidth = 338
			},
			// 将滑动条重新至于最左侧
			reset() {
				this.$refs.moveDiv.style.left = 0 + 'px';
				this.$refs.drag_bg.style.width = 0 + 'px';
				this.$refs.drag_text.style.color = '#333';
				this.confirmSuccess = false;
				this.mousePressing = false;
				this.showType = true
				this.maxwidth = this.$refs.dragDiv.clientWidth - this.$refs.moveDiv.clientWidth;
				document.getElementsByTagName('html')[0].addEventListener('mousemove', this.mouseMoveFn);
				document.getElementsByTagName('html')[0].addEventListener('mouseup', this.moseUpFn);
			}
		}
	};
</script>

<style scoped>
	.slide-check-tip {
		position: absolute;
		display: inline-block;
		top: 0px;
		right: -85px;
		/* border: 1px solid red; */
		color: #999;
		font-size: smaller;
	}

	.drag {
		position: relative;
		background-color: #e8e8e8;
		width: 70%;
		height: 34px;
		line-height: 34px;
		text-align: center;
	}

	.handler {
		width: 40px;
		height: 32px;
		border: 1px solid #ccc;
		cursor: move;
	}

	.handler_bg {
		background: #fff url('data:image/png;base64,') no-repeat center;
	}

	.handler_ok_bg {
		background: #fff url('data:image/png;base64,') no-repeat center;
	}

	.drag_bg {
		background-color: #7ac23c;
		height: 34px;
		width: 0px;
	}

	.drag_text {
		position: absolute;
		top: 0px;
		width: 100%;
		color: #333;
		text-align: center;
		-moz-user-select: none;
		-webkit-user-select: none;
		user-select: none;
		-o-user-select: none;
		-ms-user-select: none;
	}
</style>
